body{
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", Helvetica, sans-serif;
}
.App{
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.toggleFormBtn, .loadingCard{
  position: fixed;
  top: 0;
  width: 400px;
  text-align: center;
  border-radius: 0 0 10px 10px;
  background-color: sandybrown;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.toggleFormBtn > img{
  width: 30px;
  height: 30px;
}
.toggleFormBtn > p{
  padding: 0 10px;
}
.loadingCard{
  display: flex;
  justify-content: center;
  align-items: center;
}
.loadingCard > img{
  width: 50px;
  height: 50px;
}
h2{
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 60px;
}
h2 > img{
   width: 30px;
  height: 30px;
  padding-right: 16px;
}
form{
  width: 400px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: blueviolet;
  position: absolute;
  top: 150px;
}

img{
  width: 60%;
  height: auto;
  margin: 10px 0;
}
input{
  width: 60%;
  padding: 12px;
}

button{
  padding: 12px;
  color: white;
  outline: none;
  border: none;
  background-color: salmon;
  margin: 5px 0;
  border-radius: 3px;
  cursor: pointer;
  margin: 10px 0;
}

.aboutCard{
  width: 80%;
  color: white;
}

.aboutCard > a{
  color: salmon;
}